}
.pr-form-item-label {
  text-align: right;
  font-size: 14px;
}
.pr-form-item-label.pr-required,
.pr-form-item-label .pr-required {
  position: relative;
}
.pr-form-item-label.pr-required::after,
.pr-form-item-label .pr-required::after {
  position: absolute;
  content: '*';
  color: #f00;
}
.pr-form-item-label.pr-required::after {
  top: 2px;
  left: -8px;
}
.pr-form-item-label .pr-required {
  display: inline-block;
  width: 14px;
  height: 14px;
}
.pr-form-item-label .pr-required::after {
  top: 0;
  right: 0;
}
.pr-form-item-input_readonly .dy-input input,
