**/
.pr-ellipsis {
  display: block;
  width: inherit;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.pr-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}
.pr-clearfix {
  zoom: 1;
}
.pr-clearfix::after {
  display: block;
  content: '';
  clear: both;
}
.pr-hairline-top,
.pr-hairline-bottom,
.pr-hairline-left,
.pr-hairline-right {
  position: relative;
}
.pr-hairline-top::after,
.pr-hairline-bottom::after,
.pr-hairline-left::after,
.pr-hairline-right::after {
  position: absolute;
  content: '';
  background: #eee;
}
.pr-hairline-top::after {
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.pr-hairline-bottom::after {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.pr-hairline-left::after {
  left: 0;
  width: 1px;
  height: 100%;
  -webkit-transform: scaleX(0.5);
  transform: scaleX(0.5);
}
.pr-hairline-right::after {
  right: 0;
  width: 1px;
  height: 100%;
  -webkit-transform: scaleX(0.5);
  transform: scaleX(0.5);
}
