.pr-btn {
  position: relative;
  display: inline-block;
  text-align: center;
  line-height: 1;
  border-style: solid;
  border-width: 1px;
  border-color: #ddd;
  white-space: nowrap;
}
.pr-btn:after {
  display: none;
  content: '';
  position: absolute;
  top: -1px;
  left: -1px;
  bottom: -1px;
  right: -1px;
  border-radius: inherit;
  border: 0 solid transparent;
  opacity: 0.4;
  -webkit-animation: buttonEffect 0.25s;
