@-webkit-keyframes buttonEffect {
  to {
    opacity: 0;
    top: -8px;
    left: -8px;
    bottom: -8px;
    right: -8px;
    border-width: 8px;
  }
}
@keyframes buttonEffect {
  to {
    opacity: 0;
    top: -8px;
    left: -8px;
    bottom: -8px;
    right: -8px;
    border-width: 8px;
  }
}
@-webkit-keyframes loading {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
